body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    }
  
    * {
    font-family: "Verdana", cursive, sans-serif;
    color: #ffffff;
    }
    .quiz-cont {
    background-color: #8c9edf;
    width: 450px;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    justify-content: space-evenly;
    margin: 7rem 9px;
    }
    .score-section {
    display: flex;
    font-size: 24px;
    align-items: center;
    }
    /* QUESTION/TIMER/LEFT SECTION */
    .question-section {
    width: 100%;
    position: relative;
    margin: 0 1rem;
    }
  .question-count {
  margin-bottom: 20px;
  color: black;
  }
  .question-count span {
  font-size: 28px;
  color: black;
  }
  .question-text {
  margin-bottom: 12px;
  color: black;
  }
  .timer-text {
  background: rgb(230, 153, 12);
  padding: 15px;
  margin-top: 20px;
  margin-right: 20px;
  border: 5px solid rgb(255, 189, 67);
  border-radius: 15px;
  text-align: center;
  }
  /* ANSWERS/RIGHT SECTION */
  .answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  }
  .quiz-btn {
  width: 100%;
  font-size: 12px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
  }
  .correct {
  background-color: #2f922f;
  }
  .incorrect {
  background-color: #ff3333;
  }
  button:hover {
  background-color: #555e7d;
  }
  button:focus {
  outline: none;
  }
  button svg {
  margin-right: 5px;
  }
  